 //the dataset.location was got from the data attribute in the html

//console.log(locations);


export const displayMap = locations => {
    mapboxgl.accessToken =
    'pk.eyJ1IjoiYmFkZXgiLCJhIjoiY2thZzZlZmtnMDNsZTJzcWJncG1pa3ltaSJ9.Wp0A7XGxkP2vPIhsRr5sNw';
  
    var map = new mapboxgl.Map({
      container: 'map',
      style: 'mapbox://styles/badex/ckag823a40fm91imx5dcje4e4',
      scrollZoom: false
      // center: [-118.113491, 34.111745],
      // zoom: 10,
      // interactive: false
    });
  
    const bounds = new mapboxgl.LngLatBounds();
  
    locations.forEach(loc => {
      // Create marker
      const el = document.createElement('div');
      el.className = 'marker';
  
      // Add marker
      new mapboxgl.Marker({
        element: el,
        anchor: 'bottom'
      })
        .setLngLat(loc.coordinates)
        .addTo(map);
  
      // Add popup
      new mapboxgl.Popup({
        offset: 30
      })
        .setLngLat(loc.coordinates)
        .setHTML(`<p>Day ${loc.day}: ${loc.description}</p>`)
        .addTo(map);
  
      // Extend map bounds to include current location
      bounds.extend(loc.coordinates);
    });
  
    map.fitBounds(bounds, {
      padding: {
        top: 200,
        bottom: 150,
        left: 100,
        right: 100
      }
    });
  }
  